.home {
    padding: 2rem;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .hero {
    text-align: center;
    padding: 4rem 0;
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .hero h1 {
    font-size: 3.5rem;
    margin-bottom: 1rem;
    color: #00b894;
    font-weight: 700;
    min-height: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .cursor {
    display: inline-block;
    margin-left: 3px;
    animation: blink 1s step-end infinite;
  }
  
  @keyframes blink {
    from, to { opacity: 1; }
    50% { opacity: 0; }
  }
  
  .hero h2 {
    font-size: 2rem;
    color: #2d3436;
    margin-bottom: 1rem;
  }
  
  .hero p {
    font-size: 1.2rem;
    color: #636e72;
    max-width: 600px;
    margin-bottom: 2rem;
    line-height: 1.6;
  }
  
  .cta-buttons {
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .cta-primary, .cta-secondary {
    padding: 0.8rem 2rem;
    border-radius: 25px;
    text-decoration: none;
    font-weight: 500;
    transition: all 0.3s ease;
  }
  
  .cta-primary {
    background-color: #00b894;
    color: white;
    box-shadow: 0 4px 6px rgba(0, 184, 148, 0.2);
  }
  
  .cta-secondary {
    border: 2px solid #00b894;
    color: #00b894;
  }
  
  .cta-primary:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 8px rgba(0, 184, 148, 0.3);
  }
  
  .cta-secondary:hover {
    background-color: rgba(0, 184, 148, 0.1);
  }
  
  .skills-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 2rem;
    margin-top: 3rem;
  }
  
  .skill-category {
    background: white;
    padding: 2rem;
    border-radius: 12px;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
    transition: transform 0.3s ease;
  }
  
  .skill-category:hover {
    transform: translateY(-5px);
  }
  
  .skill-category h4 {
    color: #00b894;
    margin-bottom: 1.5rem;
    font-size: 1.2rem;
  }
  
  .skill-category span {
    display: inline-block;
    background: #f8f9fa;
    color: #2d3436;
    padding: 0.5rem 1rem;
    margin: 0.5rem;
    border-radius: 20px;
    font-size: 0.9rem;
    border: 1px solid #e9ecef;
  }

  .profile-photo {
    width: 220px;
    height: 220px;
    margin-bottom: 2.5rem;
    border-radius: 50%;
    padding: 5px;
    background: linear-gradient(45deg, #00b894, #00d1a0);
    box-shadow: 0 10px 30px rgba(0, 184, 148, 0.2);
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }
  
  .profile-photo-container {
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
    border: 4px solid white;
  }
  
  .profile-photo-main,
  .profile-photo-alt {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: opacity 0.3s ease;
  }
  
  .profile-photo-alt {
    opacity: 0;
  }
  
  .profile-photo:hover {
    transform: scale(1.05);
    box-shadow: 0 15px 40px rgba(0, 184, 148, 0.3);
  }
  
  .profile-photo:hover .profile-photo-alt {
    opacity: 1;
  }
  
  .skill-icons {
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    justify-content: center;
    padding: 1rem;
  }
  
  .skill-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    transition: transform 0.3s ease;
  }
  
  .skill-item:hover {
    transform: translateY(-5px);
  }
  
  .skill-item img {
    width: 40px;
    height: 40px;
  }
  
  .skill-item span {
    font-size: 0.8rem;
    color: #2d3436;
  }
  
  .skills h3 {
    text-align: center;
    color: #00b894;
    margin-bottom: 2rem;
    font-size: 2rem;
  }
  
  .skill-category h4 {
    text-align: center;
    color: #00b894;
    margin-bottom: 1.5rem;
    font-size: 1.2rem;
  }

section {
  padding: 5rem 0;
  scroll-margin-top: 70px; /* Accounts for fixed navbar */
}

.hero-section {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.experience-section,
.projects-section,
.contact-section {
  max-width: 1200px;
  margin: 0 auto;
  padding: 5rem 2rem;
}

.scroll-top-btn {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  background: #00b894;
  color: white;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transform: translateY(100px);
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 184, 148, 0.2);
  z-index: 1000;
}

.scroll-top-btn.visible {
  opacity: 1;
  transform: translateY(0);
}

.scroll-top-btn:hover {
  transform: translateY(-3px);
  background: linear-gradient(45deg, #00b894, #00d1a0);
  box-shadow: 0 6px 20px rgba(0, 184, 148, 0.3);
}

.skill-card {
  perspective: 1000px;
  cursor: pointer;
  height: 150px;
  width: 100%;
}

.skill-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.skill-card.flipped .skill-card-inner {
  transform: rotateY(180deg);
}

.skill-card-front,
.skill-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: white;
  border-radius: 12px;
  padding: 1rem;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.skill-card-back {
  transform: rotateY(180deg);
}

.skill-level {
  width: 100%;
  height: 10px;
  background: #f0f0f0;
  border-radius: 5px;
  margin: 1rem 0;
}

.skill-bar {
  height: 100%;
  background: linear-gradient(45deg, #00b894, #00d1a0);
  border-radius: 5px;
  transition: width 0.3s ease;
}

.title-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin: 2rem 0;
}

.title-container h1 {
  font-size: 3.5rem;
  color: #2d3436;
  font-weight: 800;
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
  gap: 10px;
}

.title-container h1 span {
  background: none;
  -webkit-background-clip: unset;
  -webkit-text-fill-color: unset;
  font-size: 3rem;
}

.subtitle-container {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  max-width: 800px;
  margin: 0 auto;
}

.greeting {
  font-size: 1.8rem;
  color: #2d3436;
  font-weight: 600;
  white-space: nowrap;
}

.rotating-titles {
  position: relative;
  height: 5rem;
  width: 600px;
  text-align: left;
}

.rotating-title {
  position: absolute;
  width: 100%;
  font-size: 1.8rem;
  line-height: 1.3;
  color: #00b894;
  font-weight: 700;
  margin: 0;
  opacity: 0;
  transform: translateY(20px);
  transition: all 0.5s ease;
  text-align: left;
}

.rotating-title.active {
  opacity: 1;
  transform: translateY(0);
  background: linear-gradient(45deg, #00b894, #00d1a0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.rotating-title:not(.active) {
  transform: rotateX(90deg);
}

/* Add a gradient animation to the active title */
.rotating-title.active {
  background: linear-gradient(45deg, #00b894, #00d1a0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: gradientShift 3s ease infinite;
}

@keyframes gradientShift {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

.wave-emoji {
  display: inline-block;
  animation: wave 2.5s infinite;
  transform-origin: 70% 70%;
  margin-left: 10px;
}

@keyframes wave {
  0% { transform: rotate(0deg); }
  10% { transform: rotate(14deg); }
  20% { transform: rotate(-8deg); }
  30% { transform: rotate(14deg); }
  40% { transform: rotate(-4deg); }
  50% { transform: rotate(10deg); }
  60% { transform: rotate(0deg); }
  100% { transform: rotate(0deg); }
} 